
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import MyCell from '@/components/common/MCell'
import AppFooter from '@/components/common/MFooter'


export default {
name:'my-cell-vw',
components:{
ViewBox,
Tab,
TabItem,
Group,
MyCell,
Search,
AppFooter,
XHeader,
Actionsheet,
Scroller,
Spinner
},
methods:{
handler(){

this.showMenu = true;
},
onMenuClick(){
console.log('remind on-menu-click')
},
actClick(key,item){
console.log(key)
if(key=='cancel')
return
this.changeMenu(key)
},
changeMenu(menuKey){
if(this.currMemu == menuKey)
return
this.currMemu = menuKey

if(this.pageIndex < 1){
this.$refs.myScroll.enablePullup()
this.$nextTick(() => {
this.$refs.myScroll.reset({top:0})
})
this.loadReminds()
}

},
loadReminds(){
this.pageIndex++;
console.log(this.pageIndex)
queryInfo({mod:'getRemindList',con:{ type:this.currMemu,pageIndex:this.pageIndex } }).then((res)=>{
var reminds = res.data.reminds
console.log(reminds)
this.reminds = this.reminds.concat( reminds)
setTimeout(() => {
this.$refs.myScroll.donePullup()



if(!reminds||reminds.length<10)
this.$refs.myScroll.disablePullup()
}, 100)
})
}
},
computed:{
reminds:{
get(){
return this.remindsCfg[this.currMemu].data;
},
set(val){
this.remindsCfg[this.currMemu].data = val
}
},
pageIndex:{
get(){
return this.remindsCfg[this.currMemu].pageIndex;
},
set(val){
this.remindsCfg[this.currMemu].pageIndex = val
}
},
title(){
return this.remindsCfg[this.currMemu].title;
},
menus(){
var m = {}
for(var  i in this.remindsCfg){
m[i] = this.remindsCfg[i].title
}
console.log(m)
return m
}
},
data(){
return {
showMenu:false,





pullupCfg: {
pullupStatus: 'default'
},
remindsCfg:{
menus1:{data:[],pageIndex:0,title:'大额资金变动(100万以上)'},
menus2:{data:[],pageIndex:0,title:'专项理财产品到期'},
menus3:{data:[],pageIndex:0,title:'客户生日'}
},

currMemu:'menus1',
footData:[]
}
},
mounted(){
this.loadReminds();
queryInfo({mod:"getVipFoot"}).then((res)=>{
this.footData = res.data;
})
}
}



